import {
  Card,
  Breadcrumb,
  Form,
  Button,
  Radio,
  Input,
  Upload,
  Space,
  Select,
  message,
} from 'antd'
import { PlusOutlined } from '@ant-design/icons'
import { Link, useSearchParams } from 'react-router-dom'
import './index.scss'
// 
import ReactQuill from 'react-quill'
import 'react-quill/dist/quill.snow.css'
import { useEffect, useState } from 'react'
import { fetchPublish } from '@/apis/postPublish'
import useChannels from '@/hooks/useChannels'
import { edidArticle, getArticleDateil } from '@/apis/Article'

const { Option } = Select

const Publish = () => {
  // 
  const { channels } = useChannels()

  // 
  const [imgType, setImgType] = useState(0)
  const [imageList, setImageList] = useState([])
  const onTypeChange = (v) => {
    setImageList([])
    setImgType(v.target.value)
  }
  const onUploadChange = (info) => {
    setImageList(info.fileList)
  }

  // / 
  const onFinish = async (formValue) => {
    if (imgType !== imageList.length)
      return message.warning('')
    formValue.cover = {
      type: imgType, // 
      images: imageList.map((item) =>
        item.response ? item.response.data.url : item.url
      ), // --url 
    }
    // API
    if (articleId) {
      await edidArticle(articleId, { ...formValue })
    } else {
      await fetchPublish(formValue)
    }
    message.success(articleId ? '' : '')
  }

  // 
  const [searchParams] = useSearchParams()
  const articleId = searchParams.get('id')
  const [form] = Form.useForm()
  useEffect(() => {
    const getDatetail = async () => {
      if (articleId) {
        const { data } = await getArticleDateil(articleId)
        const { cover, ...formValue } = data
        // 
        form.setFieldsValue({ ...formValue, type: cover.type })
        setImgType(cover.type)
        setImageList(cover.images.map((url) => ({ url })))
      }
    }
    getDatetail()
  }, [form, articleId])

  return (
    <div className='publish'>
      <Card
        title={
          <Breadcrumb
            items={[
              { title: <Link to={'/'}></Link> },
              { title: articleId ? '' : '' },
            ]}
          />
        }
      >
        <Form
          labelCol={{ span: 4 }}
          wrapperCol={{ span: 16 }}
          initialValues={{ type: 0 }}
          onFinish={onFinish}
          form={form}
        >
          {/*  */}
          <Form.Item
            label=''
            name='title'
            rules={[{ required: true, message: '' }]}
          >
            <Input placeholder='' style={{ width: 400 }} />
          </Form.Item>

          {/*  */}
          <Form.Item
            label=''
            name='channel_id'
            rules={[{ required: true, message: '' }]}
          >
            <Select placeholder='' style={{ width: 400 }}>
              {channels.map((item) => {
                return (
                  <Option key={item.id} value={item.id}>
                    {item.name}
                  </Option>
                )
              })}
            </Select>
          </Form.Item>

          {/*  */}
          <Form.Item label=''>
            <Form.Item name='type'>
              <Radio.Group onChange={onTypeChange}>
                <Radio value={1}></Radio>
                <Radio value={3}></Radio>
                <Radio value={0}></Radio>
              </Radio.Group>
            </Form.Item>
            {imgType > 0 && (
              <Upload
                name='image'
                listType='picture-card'
                showUploadList
                action={'http://geek.itheima.net/v1_0/upload'}
                onChange={onUploadChange}
                fileList={imageList}
              >
                <div style={{ marginTop: 8 }}>
                  <PlusOutlined />
                </div>
              </Upload>
            )}
          </Form.Item>

          {/*  */}
          <Form.Item
            label=''
            name='content'
            rules={[{ required: true, message: '' }]}
          >
            {/*  */}
            <ReactQuill
              className='publish-quill'
              theme='snow'
              placeholder=''
            />
          </Form.Item>

          <Form.Item wrapperCol={{ offset: 4 }}>
            <Space>
              <Button size='large' type='primary' htmlType='submit'>
                {articleId ? '' : ''}
              </Button>
            </Space>
          </Form.Item>
        </Form>
      </Card>
    </div>
  )
}

export default Publish
